import React, { Component } from "react";
import "./Cardbf.css";

export default class Cardbf extends Component {
  render() {
    return (
      <div className="card c1" style={{ width: "18rem" }}>
        <div className="card-body pt-0 pb-0 pl-0 pr-0 cc1">
          {this.props.img && <img src={this.props.img} alt="" />}
        </div>
      </div>
    );
  }
}
