import React from "react";
import FeatureCards from "./FeatureCards";
import "./Fullcard.css";

function Fullcard({ title, reqby, location }) {
  return (
    <div className="container col-md-12 col-lg-12 col-sm-12 col-xs-12 col-12 ">
      <FeatureCards location={location} title={title} reqby={reqby} />
    </div>
  );
}

export default Fullcard;
