import React, { Component } from "react";
import Cardbf from "./Cardbf";
import timethesale from "../images/timethesale.png";
import "./Business.css";
import usingabroker from "../images/usingabroker.png";
import usingabroker1 from "../images/usingabroker1.png";

export default class Buisnesscard extends Component {
  render() {
    return (
      <div className="slider d-flex flex-wrap">
        <div>
          <Cardbf img={timethesale} />
        </div>
        <div>
          <Cardbf img={usingabroker} />
        </div>
        <div>
          <Cardbf img={usingabroker1} />
        </div>
      </div>
    );
  }
}
