import React from "react";
import "./card.css";
import img from "../images/img.png";

function FeatureCards({ desc, title, subtitle, location, reqby }) {
  return (
    <div class="card col-lg-12 fun__box">
      <div class="card-body">
        <h5 class="card-title mt-3 text-center pb-3">{title}</h5>
        {subtitle && <h6 class="card-subtitle mb-2 text-muted">{subtitle}</h6>}
        {desc && <p class="card-text">{desc}</p>}
        <p class="card-link ml-auto mr-auto text-center">{location}</p>
        <p class="card-link text-center">{reqby}</p>
      </div>
    </div>
  );
}

export default FeatureCards;
