import React from "react";
import "./HeaderSearch.css";

function HeaderSearch() {
  return (
    <>
      <div className="row no-gutters ml-auto mr-auto d-flex justify-content-center mt-5 pt-5 mb-5 pb-5">
        <div className="col-lg-4 col-sm-10 col-8 container__input  ">
          <input
            type="text"
            className="set__input mr-0"
            placeholder="e.g. Top Buyers"
          />
        </div>
        <button className="col-lg-2 col-sm-3 col-3 set__button ">Search</button>
      </div>
    </>
  );
}

export default HeaderSearch;
