import React, { Component } from "react";
import "./CardFranchise.css";

export default class FranchiseCard extends Component {
  render() {
    return (
      <>
        {this.props.img && (
          <div className="col-lg-12 cx-border col-12 imag__in">
            <img src={this.props.img} alt="" />
          </div>
        )}

        {this.props.text && (
          <div className="col-lg-12 cx-border col-12 franchise__text">
            <i className="">{this.props.text}</i>
          </div>
        )}
      </>
    );
  }
}
