import React, { Component } from "react";

export default class AdviceCard extends Component {
  render() {
    return (
      <div class="card mt-2">
        <img src={this.props.img} class="card-img-top" alt="..." />

        <div class="card-body ">
          {this.props.title && (
            <div class="card-text pd-0">{this.props.title}</div>
          )}

          {this.props.link && (
            <div class="card-text text-primary">{this.props.link}</div>
          )}

          {this.props.text && <div class="card-text ">{this.props.text}</div>}
        </div>
      </div>
    );
  }
}
