import React, { Component } from "react";
import Slider from "react-slick";
import Buisnesscard from "./Businesscard";
import "./bfslider.css";
export default class Bfslider extends Component {
  render() {
    var settings = {
      dots: true,
      infinite: true,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1,
    };
    return (
      <>
        <div className="container mt-5 pt-5  ">
          <div className="h2 clo-lg-2 text-center">
            How do I Sell My Buisness.
          </div>
        </div>

          <div className=" d-flex justify-content-center mb-5 pb-5 ">
            <Slider {...settings} className="col-lg-10">
              <div>
                <Buisnesscard />
              </div>

              <div>
                <Buisnesscard />
              </div>

              <div>
                <Buisnesscard />
              </div>
            </Slider>
          </div>
      </>
    );
  }
}
